.container {
    max-width: $website-width;
    padding: 0 1.5rem;
    margin: auto;
    overflow: hidden;
}

// Buttons

%btn-shared {
    display: inline-block;
    padding: 0.8rem 2rem;
    transition: all 0.5s;
    border: none;
    cursor: pointer;
}

.btn {
    &-main {
        @extend %btn-shared;
        color: #333;
        background-color: $main-color;
    }

    &-light {
        @extend %btn-shared;
        color: #333;
        background-color: $light-color;
    }

    &-dark {
        @extend %btn-shared;
        color: #333;
        background-color: $dark-color;
    }
}

button[class^='btn-']:hover,
a[class^='btn-']:hover,
input[class^='btn-']:hover {
    background-color: $main-color;
}

.bg {

    &-main {
        background: $main-color;
        color: set-text-color($main-color);
    }

    &-light {
        background: $light-color;
        color: set-text-color($light-color);
    }

    &-dark {
        background: $dark-color;
        color: set-text-color($dark-color);
    }

    &-medium {
        background: $medium-color;
        color: set-text-color($medium-color);
    }
}

.lead {
    font-size: 1.3rem;
    margin-bottom: 2rem;
}

.text-center {
    text-align: center;
}

// Padding
.py {

    &-1 {
        padding: 1rem 0rem;
    }

    &-2 {
        padding: 2rem 0rem;
    }

    &-3 {
        padding: 3rem 0rem;
    }

    &-4 {
        padding: 4rem 0rem;
    }
}

// Margin
.my {

    &-1 {
        padding: 1rem 0rem;
    }

    &-2 {
        padding: 2rem 0rem;
    }

    &-3 {
        padding: 3rem 0rem;
    }

    &-4 {
        padding: 4rem 0rem;
    }
}

//Section Title 
.section-title {
    font-size: 2rem;
    display: block;
    padding-bottom: 0.5rem;
    text-align: center;
    font-weight: 700;
    text-transform: uppercase;
}

// Bottom Line
.bottom-line {
    height: 2px;
    width: 3rem;
    background: $main-color;
    display: block;
    margin: 0 auto 1rem auto;
}